<template>
    <div>
        <div class="login_container" v-show="showHide.loginShow">
            <div class="login_box">
                <div class="title-container">
                    <h3 class="title">泊特云对讲</h3>
                </div>
                <el-form :model="loginFrom">
                    <el-form-item prop="username">
                        <el-input v-model="loginFrom.username" placeholder="请输入用户名"></el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input type="password" v-model="loginFrom.password" placeholder="密码"></el-input>
                    </el-form-item>
                    <el-form-item class="btns">
                        <el-button type="primary" class="btn-login" @click="login">登录</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>

        <el-container class="container" v-show="showHide.indexShow">
            <el-header>云坐席Demo2</el-header>
            <el-container>
                <el-aside width="500px" style="border: 1px ;">
                    <h1 style="line-height: 30px">设备列表</h1>
                    <table border="1">
                        <tr style="line-height: 80px">
                            <th>设备Id</th>
                            <th>设备名称</th>
                            <th>设备类型</th>
                            <th>设备状态</th>
                            <th>操作</th>
                        </tr>
                        <tr style="line-height: 30px" v-for="(item,index) in deviceList" :key="index">
                            <td height="50px">{{item.deviceId}}</td>
                            <td height="50px">{{item.deviceName}}</td>
                            <td height="50px">{{item.deviceType}}</td>
                            <td height="50px">{{item.action}}</td>
                            <td>
                                <el-button @click="clickDeviceId(item.deviceId)" type="primary">呼叫</el-button>
                            </td>
                        </tr>
                    </table>
                </el-aside>
                <el-main>
                    <div v-show="showHide.operate">
                        <el-button @click="openTheGate" type="primary">开闸</el-button>
                        <el-button @click="closeTheGate" type="primary">关闸</el-button>
                        <el-button @click="intercom" type="primary">DH-对讲</el-button>
                        <el-button @click="closeRT" type="primary">关闭</el-button>
                        <br/>
                        <h1>对讲视频</h1>
                        <video v-show="showHide.dhvideo" style="height: 280px" ref="box1"></video>
                    </div>


                </el-main>
                <el-aside width="450px" style="border: 1px ;">
                    <h1 style="line-height: 30px">通知列表</h1>
                    <table border="1">
                        <tr style="line-height: 80px">
                            <!--<th>设备Id</th>-->
                            <th>设备名称</th>
                            <!--<th>设备类型</th>-->
                            <th>设备状态</th>
                            <th>操作</th>
                        </tr>
                        <tr style="line-height: 30px" v-for="(item,index) in onNotifyList" :key="index">
                            <td height="50px">{{item.deviceId}}</td>
                            <td height="50px">{{item.deviceName}}</td>
                            <td height="50px">{{item.deviceType}}</td>
                            <td height="50px">{{item.action}}</td>
                            <td>
                                <el-button @click="clickDeviceIds(item.deviceId)" type="primary">呼叫</el-button>
                            </td>
                        </tr>
                    </table>
                </el-aside>

            </el-container>
        </el-container>


    </div>

</template>


<script async>

    export default {
        name: "Login",
        data() {
            return {
                //控制元素隐藏对象
                showHide: {
                    loginShow: true,
                    indexShow: false,
                    //判断是否登录
                    isLogin: true,
                    //dh-视频是否显示
                    dhvideo: false,
                    //操作面板是否打开
                    operate: false,
                    //通知是否显示
                    callShow:true
                },

                //登录状态
                loginStatus: '',
                //作用，获取点击设备id
                id: '',

                //红门证书列表环境
                // loginFrom: {
                //     username: 'ganhaixiong',
                //     password: '123456',
                //     serverAdds: 'dh.hmzhtc.com',
                //     port: '443',
                // },
                loginFrom: {
                    username: 'ceshi1',
                    password: 'ceshi1',
                    serverAdds: 'pasikapark.xyz',
                    port: '8088',
                },
                onNotifyList:[],
                user: {},
                count: [],
                deviceList: [],

                // count:[{}],
                // eslint-disable-next-line no-undef
                dhWeb: new DHAlarmWeb()
            };
        },

        created() {
            console.log("原始对象数组",this.count)
            console.log("真实对象数组",this.deviceList)
        },

        //钩子函数
        mounted() {
            console.log("------------mounted------------");
            //登录回调
            this.dhWeb.onLogin = (message) => {
                this.onLogin(message);
            }
            //列表回调
            this.dhWeb.onDeviceList = (message) => {
                this.onDeviceList(message);
            }
            //通知回调
            this.dhWeb.onNotify = (message) => {
                this.onNotify(message);
            }

            //刷新重登
            let isLogin = sessionStorage.getItem("isLogin");
            let username = sessionStorage.getItem("dhUname");
            let password = sessionStorage.getItem("dhPwd");
            // alert(isLogin + username + password)
            if (isLogin === "true" && username != null && password != null) {
                this.login()
            }
        },


        updated(){
        },
        //方法区
        methods: {
            // 登录方法
            login() {
                this.dhWeb.setWebsocketPort({dataWsPort: this.loginFrom.port, mediaWsPort: this.loginFrom.port});//端口
                this.dhWeb.login(this.loginFrom.username, this.loginFrom.password, this.loginFrom.serverAdds);//从data获取数据，赋值给login执行
                sessionStorage.setItem("dhUname", this.loginFrom.username);
                sessionStorage.setItem("dhPwd", this.loginFrom.password);
                sessionStorage.setItem("dhServerIp", this.loginFrom.serverAdds);
                sessionStorage.setItem("dhPort", this.loginFrom.port);
                localStorage.setItem("dhUname", this.loginFrom.username);
                localStorage.setItem("dhPwd", this.loginFrom.password);
                localStorage.setItem("dhServerIp", this.loginFrom.serverAdds);
                localStorage.setItem("dhPort", this.loginFrom.port);
            },
            onDeviceList(data) {
                let deviceList = data.params.list;
                this.deviceList.push(...deviceList);
            },


            // 获取设备列表通知，改方法回获取设备列表的调用，并且拿到更新设备的状态
            onNotify: function (data) {
                let params = data.params;
                this.count.push(params)

                //4行代码（思路）花了我10元人民币！！！！！！！！！！！！
                this.count.forEach((i)=>{
                    this.deviceList.forEach((s)=>{
                        if (s.deviceId==i.deviceId&&s!=null){
                            Object.assign(s,i)
                        }
                    })
                })
                var did = params.deviceId;
                this.id=did;
                if (params.action=="Start"){
                    this.$message({
                        message: '呼叫来了' ,
                        type: 'success'
                    });
                    //获取设备列表的单、多个个列表
                    this.deviceList.forEach(item=>{
                        if (item.deviceId==did){
                            console.log(item)
                            this.onNotifyList.push(item)
                        }
                    })

                }else if (params.action=="Dealing"){
                    this.$message({
                        message: '呼叫正在处理' ,
                        type: 'success'
                    });
                    this.deviceList.forEach(item=>{
                        if (item.deviceId==did){
                            console.log(item)
                            this.onNotifyList.splice(item)
                        }
                    })
                }


            },
            /**
             * 获取id
             * @param index
             */
            clickDeviceId(index) {
                this.id = index;
                this.showHide.operate = true;
            },
            clickDeviceIds(){
                this.showHide.operate = true;
                this.dhWeb.startTalk(this.id);
                this.showHide.dhvideo = true;
                this.dhWeb.playRT(this.$refs.box1, this.id, sessionStorage.getItem('loginHandle'), true);
                this.deviceList.forEach(item=>{
                    if (item.deviceId==this.id ){
                        console.log(item)
                        this.onNotifyList.splice(item)
                    }
                })
            },
            onLogin(data) {
                if (data.error == "success") {
                    this.$message({
                        message: '登录成功' + data.error,
                        type: 'success'
                    });
                    this.showHide.indexShow = true;
                    this.showHide.loginShow = false;
                    this.loginState = "在线";
                    this.isLogin = true;
                    sessionStorage.setItem('loginHandle', data.params.loginHandle);//存入loginHandle标识
                    sessionStorage.setItem('isLogin', this.isLogin);//存入登录状态
                } else if (data.error == "repeat") {
                    if (sessionStorage.setItem("isLogin" != null)) {
                        return;
                    }
                } else if (data.error == "authfail") {
                    this.$message({
                        message: '账号密码错误',
                        type: 'error'
                    });
                } else {
                    this.$message({
                        message: '登录失败',
                        type: 'error'
                    });
                }
            },

            //开闸
            openTheGate() {
                let id = this.id;
                this.dhWeb.doControl(id, sessionStorage.getItem("loginHandle"), 1);
                console.log("开闸成功")
            },
            //关闸
            closeTheGate() {
                let id = this.id;
                this.dhWeb.doControl(id, sessionStorage.getItem('loginHandle'), 2);
                console.log("关闸成功")
            },
            //对讲
            intercom() {
                let id = this.id;
                console.log(id)
                this.dhWeb.startTalk(id);
                this.showHide.dhvideo = true;
                this.dhWeb.playRT(this.$refs.box1, id, sessionStorage.getItem('loginHandle'), true);
            },
            //关闭
            closeRT() {
                let id = this.id;
                console.log(id)
                this.showHide.dhvideo = false;
                this.showHide.operate = false;
                this.dhWeb.stopRT(id, sessionStorage.getItem('loginHandle'))
            },

        }


    }
</script>

<style scoped>
    .el-header, .el-footer {
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 60px;
    }

    .el-aside {
        background-color: #D3DCE6;
        color: #333;
        text-align: center;
        line-height: 200px;
    }

    .el-main {
        background-color: #E9EEF3;
        color: #333;
        text-align: center;
        line-height: 160px;
    }

    .container {
        height: 900px;
    }

    .login_container {
        height: 900px;
        background-color: #6ad2a2;
    }

    .login_box {
        width: 400px;
        height: 350px;
        padding: 35px 35px 35px 35px;
        position: absolute;
        transform: translate(150%, 50.5%);
        background-color: #fff;
        box-shadow: 0 0 25px #cac6c6;
        border: 1px solid #eaeaea;
        border-radius: 10px;
    }

    .title {
        text-align: center;
        font-size: 30px;
    }

    .btn-login {
        width: 100%;
        border-radius: 30px;
        margin-top: 20px;
    }


</style>
